/**
 * Ïù∏Ï¶ù Ïò§Î•ò Ï≤òÎ¶¨ Ïú†Ìã∏Î¶¨Ìã∞
 * ÌäπÌûà Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞ Í¥ÄÎ†® Ïò§Î•òÎ•º Ï≤òÎ¶¨Ìï©ÎãàÎã§.
 */

import { createBrowserSupabaseClient } from '@/lib/supabase/client';

export class AuthErrorHandler {
  private static instance: AuthErrorHandler | null = null;
  private supabase = createBrowserSupabaseClient();

  static getInstance(): AuthErrorHandler {
    if (!AuthErrorHandler.instance) {
      AuthErrorHandler.instance = new AuthErrorHandler();
    }
    return AuthErrorHandler.instance;
  }

  /**
   * Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞ Ïò§Î•ò Ï≤òÎ¶¨
   */
  async handleRefreshTokenError(error: any): Promise<boolean> {
    console.warn('üîÑ [AuthError] Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞ Ïò§Î•ò Í∞êÏßÄ:', error.message);

    try {
      // 1. Î™®Îì† Ïù∏Ï¶ù Í¥ÄÎ†® Ïä§ÌÜ†Î¶¨ÏßÄ Ï†ïÎ¶¨
      await this.clearAuthStorage();

      // 2. Supabase ÏÑ∏ÏÖò Í∞ïÏ†ú Ï¢ÖÎ£å
      await this.supabase.auth.signOut({ scope: 'global' });

      // 3. ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ®ÏúºÎ°ú Íπ®ÎÅóÌïú ÏÉÅÌÉú Î≥µÏõê
      if (typeof window !== 'undefined') {
        console.log('üîÑ [AuthError] ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ®ÏúºÎ°ú ÏÑ∏ÏÖò Ï†ïÎ¶¨');
        window.location.reload();
      }

      return true;
    } catch (clearError) {
      console.error('‚ùå [AuthError] ÏÑ∏ÏÖò Ï†ïÎ¶¨ Ï§ë Ïò§Î•ò:', clearError);
      return false;
    }
  }

  /**
   * Ïù∏Ï¶ù Í¥ÄÎ†® Ïä§ÌÜ†Î¶¨ÏßÄ ÏôÑÏ†Ñ Ï†ïÎ¶¨
   */
  private async clearAuthStorage(): Promise<void> {
    if (typeof window === 'undefined') return;

    try {
      // localStorage Ï†ïÎ¶¨
      const authKeys = [
        'supabase.auth.token',
        'supabase.auth.refresh_token', 
        'supabase.auth.expires_at',
        'sb-auth-token',
        'auth_session_active',
        'auth_provider',
        'auth_timestamp',
        'auth_success'
      ];

      authKeys.forEach(key => {
        try {
          localStorage.removeItem(key);
        } catch (e) {
          console.warn(`localStorage ÌÇ§ Ï†úÍ±∞ Ïã§Ìå®: ${key}`, e);
        }
      });

      // Ìå®ÌÑ¥ Í∏∞Î∞ò ÌÇ§ Ï†úÍ±∞
      const keysToRemove: string[] = [];
      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i);
        if (key && (key.includes('supabase') || key.includes('auth') || key.includes('sb-'))) {
          keysToRemove.push(key);
        }
      }
      
      keysToRemove.forEach(key => {
        try {
          localStorage.removeItem(key);
        } catch (e) {
          console.warn(`Ìå®ÌÑ¥ ÌÇ§ Ï†úÍ±∞ Ïã§Ìå®: ${key}`, e);
        }
      });

      // Ïø†ÌÇ§ Ï†ïÎ¶¨
      const cookiesToRemove = [
        'auth-token',
        'auth-refresh-token', 
        'sb-auth-token',
        'supabase-auth-token'
      ];

      cookiesToRemove.forEach(cookieName => {
        try {
          document.cookie = `${cookieName}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
          document.cookie = `${cookieName}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/; domain=${window.location.hostname};`;
        } catch (e) {
          console.warn(`Ïø†ÌÇ§ Ï†úÍ±∞ Ïã§Ìå®: ${cookieName}`, e);
        }
      });

      console.log('‚úÖ [AuthError] Ïù∏Ï¶ù Ïä§ÌÜ†Î¶¨ÏßÄ Ï†ïÎ¶¨ ÏôÑÎ£å');
    } catch (error) {
      console.error('‚ùå [AuthError] Ïä§ÌÜ†Î¶¨ÏßÄ Ï†ïÎ¶¨ Ï§ë Ïò§Î•ò:', error);
    }
  }

  /**
   * Ïù∏Ï¶ù Ïò§Î•òÍ∞Ä Î¶¨ÌîÑÎ†àÏãú ÌÜ†ÌÅ∞ Í¥ÄÎ†®Ïù∏ÏßÄ ÌôïÏù∏
   */
  isRefreshTokenError(error: any): boolean {
    if (!error) return false;
    
    const message = error.message || '';
    return (
      message.includes('Invalid Refresh Token') ||
      message.includes('Refresh Token Not Found') ||
      message.includes('refresh_token_not_found') ||
      message.includes('invalid_refresh_token')
    );
  }

  /**
   * Ïù∏Ï¶ù ÏÉÅÌÉú Î≥µÍµ¨ ÏãúÎèÑ
   */
  async attemptRecovery(): Promise<boolean> {
    try {
      console.log('üîÑ [AuthError] Ïù∏Ï¶ù ÏÉÅÌÉú Î≥µÍµ¨ ÏãúÎèÑ');
      
      // ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê ÌôïÏù∏ (getUser()Îäî getSession()Î≥¥Îã§ Îπ†Î•¥Í≥† ÏïàÏ†ïÏ†Å)
      const { data: { user }, error } = await this.supabase.auth.getUser();
      
      if (error || !user) {
        console.log('‚ùå [AuthError] Î≥µÍµ¨ Î∂àÍ∞ÄÎä• - Î°úÍ∑∏ÏïÑÏõÉ ÏÉÅÌÉúÎ°ú Ï≤òÎ¶¨');
        return false;
      }

      console.log('‚úÖ [AuthError] ÏÇ¨Ïö©Ïûê Î≥µÍµ¨ ÏÑ±Í≥µ');
      return true;
    } catch (error) {
      console.error('‚ùå [AuthError] Î≥µÍµ¨ ÏãúÎèÑ Ï§ë Ïò§Î•ò:', error);
      return false;
    }
  }
}

/**
 * Ï†ÑÏó≠ Ïò§Î•ò Ï≤òÎ¶¨Í∏∞ - AuthProviderÏóêÏÑú ÏÇ¨Ïö©
 */
export function handleAuthError(error: any): Promise<boolean> {
  const handler = AuthErrorHandler.getInstance();
  
  if (handler.isRefreshTokenError(error)) {
    return handler.handleRefreshTokenError(error);
  }
  
  return Promise.resolve(false);
}

/**
 * Î∏åÎùºÏö∞Ï†Ä ÏΩòÏÜîÏóêÏÑú ÏàòÎèô Ïã§Ìñâ Í∞ÄÎä•Ìïú ÏùëÍ∏â Î≥µÍµ¨ Ìï®Ïàò
 */
export function emergencyAuthReset(): void {
  if (typeof window === 'undefined') {
    console.error('Î∏åÎùºÏö∞Ï†Ä ÌôòÍ≤ΩÏóêÏÑúÎßå Ïã§Ìñâ Í∞ÄÎä•Ìï©ÎãàÎã§.');
    return;
  }

  console.log('üö® [Emergency] ÏùëÍ∏â Ïù∏Ï¶ù ÏÉÅÌÉú Î¶¨ÏÖã ÏãúÏûë');

  try {
    // localStorage Ï†ÑÏ≤¥ Ï†ïÎ¶¨
    const authKeys: string[] = [];
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && (key.includes('supabase') || key.includes('auth') || key.includes('sb-'))) {
        authKeys.push(key);
      }
    }
    
    authKeys.forEach(key => localStorage.removeItem(key));
    
    // Ïø†ÌÇ§ Ï†ïÎ¶¨
    document.cookie.split(';').forEach(cookie => {
      const cookieName = cookie.trim().split('=')[0];
      if (cookieName && (cookieName.includes('auth') || cookieName.includes('supabase'))) {
        document.cookie = `${cookieName}=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
      }
    });

    console.log('‚úÖ [Emergency] ÏùëÍ∏â Î¶¨ÏÖã ÏôÑÎ£å - ÌéòÏù¥ÏßÄÎ•º ÏÉàÎ°úÍ≥†Ïπ®ÌïòÏÑ∏Ïöî');
    alert('Ïù∏Ï¶ù ÏÉÅÌÉúÍ∞Ä Î¶¨ÏÖãÎêòÏóàÏäµÎãàÎã§. ÌéòÏù¥ÏßÄÎ•º ÏÉàÎ°úÍ≥†Ïπ®Ìï©ÎãàÎã§.');
    window.location.reload();
  } catch (error) {
    console.error('‚ùå [Emergency] ÏùëÍ∏â Î¶¨ÏÖã Ïã§Ìå®:', error);
    alert('ÏùëÍ∏â Î¶¨ÏÖãÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Î∏åÎùºÏö∞Ï†ÄÎ•º ÏôÑÏ†ÑÌûà Ïû¨ÏãúÏûëÌï¥Ï£ºÏÑ∏Ïöî.');
  }
}

// Ï†ÑÏó≠ window Í∞ùÏ≤¥Ïóê ÏùëÍ∏â Ìï®Ïàò Îì±Î°ù (Í∞úÎ∞ú Î™®ÎìúÏóêÏÑúÎßå)
if (typeof window !== 'undefined' && process.env.NODE_ENV === 'development') {
  (window as any).emergencyAuthReset = emergencyAuthReset;
  console.log('üîß [Dev] emergencyAuthReset Ìï®ÏàòÍ∞Ä windowÏóê Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.');
} 